import * as jsonprovs from '../data/provinces.json';
import * as jsonfsaToProv from '../data/fsa_to_prov.json';
var provinceObj = {};
var fsaToProv = jsonfsaToProv;
var provs = jsonprovs;
var Provinces = /** @class */ (function () {
    function Provinces(language) {
        var _this = this;
        this.list = {};
        Object.keys(provs[language]).forEach(function (provKey) {
            _this.list[provKey] = provs[language][provKey];
        });
    }
    Provinces.prototype.fsaToProvinces = function (fsa) {
        var _this = this;
        var genericObj = {};
        // either a provincial code, or an array of them
        var provCodes = fsaToProv[fsa.substring(0, 1).toUpperCase()];
        if (typeof provCodes === 'number') {
            provCodes = [provCodes];
        }
        provCodes.forEach(function (n) {
            genericObj[n] = _this.list[n];
        });
        return genericObj;
    };
    return Provinces;
}());
export default function (language) {
    return provinceObj[language] = provinceObj[language] ? provinceObj[language] : new Provinces(language);
}



